.sidebarTopBarCOLOR {
  z-index: 2;
}
/* btn press magic */
#mydividas:active {
  background-color: #fdaa44;
}

#dividastotais:active {
  background-color: #fdaa44;
}

#criardivida:active {
  background-color: #fdaa44;
}

#sbbg {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0%;
  top: 0px;

  background: #fcc17a;
}

.App-logo {
  position: relative;
  width: 70%;
  left: 5%;
  top: 9%;

  /* sombra base */
  filter: drop-shadow(4px 4px 25px rgba(0, 0, 0, 0.1));
}
#criardivida a {
  width: 0% !important;
}
/* criar divida btn*/
#criardivida {
  z-index: 2;
  position: relative;
  width: 100%;
  height: 8%;
  left: 0px;
  top: 0%;
  background-color: #fcc17a;
  border-top: 1px solid #ffffff;
  border-bottom: 1px solid #ffffff;
  box-sizing: border-box;
}
.LogoScale {
  height: 55%;
}

.App-logo-plus {
  /* Criar Dívida Icone */
  position: relative;
  left: 7%;
  top: 23%;

  transform: matrix(1, 0, 0, -1, 0, 0);
}

/* ------ minhas dividas btn --------*/
#mydividasdiv {
  position: relative;
  width: 100%;
  height: 8%;
  left: 0px;
  top: 0%;
  transform-origin: top;
}

#mydividas {
  background-color: #fcc17a;
  z-index: 2;
  position: relative;
  width: 100%;
  height: 100%;
  left: 0px;
  top: 0%;

  border-bottom: 1px solid #ffffff;
  box-sizing: border-box;
  transition: 0.6s ease;

  cursor: pointer;
}

#primbotSTR {
  position: relative;
  width: 70%;
  height: 50%;
  top: -13%;
  margin-top: -3%;
  left: 20%;

  font-family: fonteLabel;
  font-style: normal;
  font-weight: 600;
  font-size: 3.3vh;
  line-height: 26px;
  display: flex;
  align-items: center;
  letter-spacing: 0.01em;

  color: #ffffff;
}

#transdivsMINHASDIVIDAS {
  position: relative;
  z-index: 1;
  height: 200%;
  transition: 0.6s ease;
}

#primbot {
  z-index: 1;
  position: relative;
  width: 100%;
  height: 50%;
  left: 0px;
  top: 0%;

  background: #f08a6e;
  border-bottom: 1px solid #ffffff;
  box-sizing: border-box;
}

.App-logo-pessoa {
  /* Minhas Dividas Ícone */

  position: relative;
  left: 7%;
  top: 25%;
}

.App-logo-UP {
  /* Seta Baixo */

  position: relative;
  left: 88%;
  top: -65%;
  transition: 0.6s ease;
}

/* ----money button ---- */
.App-logo-Money {
  position: relative;
  left: 6.5%;
  top: 25%;
}

#dividastotaisdiv {
  position: relative;
  width: 100%;
  height: 8%;
  left: 0px;
  transform-origin: top;
  transition: 0.6s ease;
}

#dividastotais {
  background-color: #fcc17a;
  z-index: 2;
  position: relative;
  width: 100%;
  height: 100%;
  left: 0px;
  top: -100%;
  border-bottom: 1px solid #ffffff;
  box-sizing: border-box;
  transition: 0.6s ease;

  cursor: pointer;
}
/*anim*/
#menu {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0px;
  transition: 0.6s ease;
}
#menu-hidden {
  top: 0%;
  margin-top: 0%;
}

#botao {
  position: relative;
  width: 100%;
  height: 100%;
  left: 0px;
  top: -100%;

  background: #f08a6e;
  border-bottom: 1px solid #ffffff;
  box-sizing: border-box;
}

.App-logo-UPBottom {
  position: relative;
  left: 88%;
  top: -19%;
  transition: 0.6s ease;
}

#subbotSTR {
  position: relative;
  width: 60%;
  height: 50%;
  top: 24%;
  left: 20.5%;

  font-family: fonteLabel;
  font-style: normal;
  font-weight: 600;
  font-size: 5.2vw;
  line-height: 26px;
  display: flex;
  align-items: center;
  letter-spacing: 0.01em;

  color: #ffffff;
}

#bottomBar {
  position: absolute;
  width: 100%;
  left: 0px;
  top: 92%;
  height: 8%;
  transition: 0.6s ease;
}

#BottomPrimary {
  z-index: 2;
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0px;
  top: 0%;
  background-color: #fcc17a;
  border-top: 1px solid #ffffff;
  box-sizing: border-box;

  cursor: pointer;
}

#bottomBarSTR {
  position: relative;
  width: 40%;
  height: 50%;
  top: 20%;
  left: 20%;

  font-family: fonteLabel;
  font-style: normal;
  font-weight: 600;
  font-size: 5.7vw;
  line-height: 26px;
  display: flex;
  align-items: center;
  letter-spacing: 0.01em;

  color: #ffffff;
}

#BottomSec {
  position: relative;
  width: 100%;
  height: 100%;
  left: 0px;
  top: 0%;

  background: #f08a6e;
  border-top: 1px solid #ffffff;
  box-sizing: border-box;
  transition: 0.6s;
}

/* para remover de todos os Links*/
a {
  position: relative;
  text-decoration: none;
  left: 0%;
  margin-left: 0%;
}

@media (min-width: 961px) {
  .LogoScale {
    height: 40%;
  }

  #sbbg {
    width: 23.15%;
  }

  #primbotSTR {
    width: 60%;
    top: -23%;
    margin-top: 0%;
    left: 27%;

    font-size: 2.5vh;
  }

  #subbotSTR {
    width: 74%;
    left: 23%;

    font-size: 2.5vh;
    cursor: pointer;
  }

  #bottomBarSTR {
    width: 80%;
    top: 22%;
    left: 7%;

    font-size: 2.5vh;
  }
}
